import { SafeUrl } from '@angular/platform-browser';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
/**
 * Generated class for the ImagemModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-imagem-modal',
  templateUrl: 'imagem-modal.html',
})
export class ImagemModalPage {

  foto: SafeUrl;
  constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl : ViewController) {

    this.foto = this.navParams.get('fotoURL');
  
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ImagemModalPage');
  }

  public closeModal(){
    this.viewCtrl.dismiss();
  }
}



